.App {
  font-family: sans-serif;
  text-align: center;
}

.formSection {
  background-color: rgb(230, 230, 230);
  height: 60vh;
  width: 60vw;
  border-radius: 0.5rem;
  margin: 5vw auto;
}

.title {
  padding-top: 2.5rem;
  color: rgb(68, 64, 64);
}

/* field design */

input {
  width: 80%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  font-size: 16px;
}

/*  button design */

button {
  background-color: #833ae0; /* Green */
  border: none;
  border-radius: 0.6rem;
  color: white;
  padding: 15px 15%;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 20px;
}
